import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SupportService {

  private data = new BehaviorSubject(0);
  private imgdata = new BehaviorSubject(null);
  currentdata = this.data.asObservable();
  currentImgdata = this.imgdata.asObservable();

  constructor() {}

  changeData(freshdata: number) {
    this.data.next(freshdata);
  }

  changeImgData(freshdata: any) {
    this.imgdata.next(freshdata);
  }
}
