import { Pagination } from '../condition/pagination';

export class QuantitaiveInfo {
    partner_id:number;
	team_status:boolean;
	admin_status:boolean;
	from_date:any;
    to_date:any;
    condition:Pagination
    
    constructor(partner_id,team_status,admin_status,from_date,to_date,condition){
        this.partner_id=partner_id;
        this.team_status=team_status;
        this.admin_status=admin_status;
        this.from_date=from_date;
        this.to_date=to_date;
        this.condition=condition;
    }
}
